body{
  background-color: teal;
}
.App{
  text-align: center;
  overflow-x: hidden;
}
.App h2{
  color: white;
  font-size: 2.4rem;
}
.to-do{
  display: flex;
  justify-content: space-between;
  margin: 1rem;
  font-size: 1.4rem;
  color: white;
  list-style: lower-roman;
  text-align: left;
  align-items: center;
}
.to-do li{
  width: 60%;
}
.to-do button{
  min-width: 70px;
  width: 40%;
  background-color: teal;
  color: white;
  outline: none;
  cursor: pointer;
}
.to-do .buttons{
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  width: 35%;
  align-items: center;
}
.to-do .buttons button{
  height: 2rem;
}
.input_box{
  display: flex;
  align-items: center;
  justify-content: space-around;
  margin-bottom: 2rem;
  flex-direction: row;
  width: 100%;
}
.input_box input{
  height: 2rem;
  min-width:150px;
  width: 40%;
}
button{
  border: none;
  box-shadow: 3px 2px 6px rgb(87, 70, 70);
}
.input_box button{
  height: 2rem;
  min-width: 120px;
  color: white;
  background-color: teal;
}
.select{
  margin-bottom: 2rem;
  width: 100%;
}
.select select{
  height: 3rem;
  width: 60%;
  background-color: teal;
  border: 1px solid white;
  color: white;
  font-size: 1.2rem;
  padding-left: 1rem;
  outline: none;
}
.completed{
  text-decoration: line-through;
  opacity: 0.6;
}
.to-do .completes{
  display: none;
}
.to-do-toggle{
  display: block;
}
::placeholder{
  padding: 1rem;
}
@media(max-width:600px){
  button{
    height: 2rem;
  }
  .to-do{
    display: flex;
    justify-content: space-between;
    margin-left: 0;
    align-items: center;
  }
  .to-do button{
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0.5rem;
    width: 30%;
  }
  .to-do li{
    width: 50%;
  }
  .input_box input{
    width: 40%;
  }
  .input_box button{
    width: 20%;
  }
  .App h2{
    font-size: 1.6rem;
  }
}
